import React from 'react';
import {Jumbotron} from 'reactstrap';
import {Link} from 'react-router-dom';

function Footer(){
    return(
        <footer className="foot">
            <Jumbotron>
                <div className="footer basic-font">
                    <div className="container">
                    <div className="row justify-content-center">             
                        <div className="col-4 offset-1 col-sm-2">
                            <h5>Links</h5>
                            <ul className="list-unstyled">
                                <li><Link to='/home'>Home</Link></li>
                                <li><Link>About Us</Link></li>                        
                                <li><Link>Contact Us</Link></li>
                            </ul>
                        </div>
                        <div className="col-7 col-sm-5">
                            <h5>Our Address</h5>
                            <address>
                            Pillai HOC College of Engineering<br />
                            Rasayani, Maharashtra<br />
                            India<br />
                            <i className="fa fa-phone fa-lg"></i>: +852 1234 5678<br />
                            <i className="fa fa-fax fa-lg"></i>: +852 8765 4321<br />
                            <i className="fa fa-envelope fa-lg"></i>: <a href="mailto:srijeetpatil7@gmail.com">
                                srijeetpatil7@gmail.com</a>
                            </address>
                        </div>
                        <div className="col-12 col-sm-4 align-self-center">
                            <div className="text-center">                        
                                <a className="btn btn-social-icon btn-facebook" ><i className="fa fa-facebook"></i></a>
                                <a className="btn btn-social-icon btn-linkedin"><i className="fa fa-linkedin"></i></a>
                                <a className="btn btn-social-icon btn-twitter"><i className="fa fa-twitter"></i></a>
                                <a className="btn btn-social-icon btn-google"><i className="fa fa-youtube"></i></a>
                                <a className="btn btn-social-icon" href="mailto:"><i className="fa fa-envelope-o"></i></a>
                            </div>
                        </div>
                    </div>
                    <div className="row justify-content-center">             
                        <div className="col-auto">
                            <p>© Copyright 2020 E Mart</p>
                        </div>
                    </div>
                </div>
                </div>
            </Jumbotron>            
        </footer>                               
    );
}

export default Footer;